* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: #444;
  height: 100vh;
  background-color: #e3fafc;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  font-family: Samim, sans-serif;
  display: flex;
}

nav {
  background-color: #d0ebff;
  border-radius: 3rem;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  padding: 3rem;
  display: flex;
  position: relative;
}

.logged-in nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  display: none;
}

.welcome {
  text-align: center;
  order: 2;
  font-size: 1.9rem;
  font-weight: 500;
}

.logged-in .welcome {
  text-align: center;
}

.logo {
  height: 7rem;
  order: 1;
  animation: finger 3s linear infinite alternate;
}

@keyframes finger {
  0% {
    fill: #f03e3e;
  }

  14.3% {
    fill: #f76707;
  }

  28.6% {
    fill: #fcc419;
  }

  42.9% {
    fill: #82c91e;
  }

  57.1% {
    fill: #3bc9db;
  }

  71.4% {
    fill: #4263eb;
  }

  85.7% {
    fill: #ae3ec9;
  }

  100% {
    fill: #e64980;
  }
}

.login {
  flex-direction: column;
  order: 3;
  gap: 2rem;
  display: flex;
}

.login__input {
  text-align: center;
  color: inherit;
  border: 1px solid #fff;
  border-radius: 10rem;
  padding: .5rem 2rem;
  font-family: inherit;
  font-size: 1.6rem;
  transition: all .3s;
}

.login__input:focus {
  border: 1px solid #ccc;
  outline: none;
}

.login__input::-ms-input-placeholder {
  color: #bbb;
}

.login__input::placeholder {
  color: #bbb;
}

.login__btn {
  color: inherit;
  cursor: pointer;
  background-color: #edf2ff;
  border: none;
  border-radius: 1000px;
  padding: .5rem 2rem;
  font-family: inherit;
  font-size: 1.6rem;
  transition: all .3s;
}

.login__btn:hover, .login__btn:focus, .btn--sort:hover, .btn--sort:focus {
  color: #777;
  outline: none;
}

.app {
  max-width: 100rem;
  opacity: 0;
  visibility: hidden;
  grid-template-rows: auto repeat(3, 15rem) auto;
  grid-template-columns: 4fr 3fr;
  gap: 2rem;
  margin: 4rem auto;
  transition: all 1s;
  display: grid;
  position: fixed;
}

.logged-in .app {
  opacity: 1;
  visibility: visible;
  position: relative;
}

.balance {
  grid-column: 1 / span 2;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 2rem;
  display: flex;
}

.balance__label {
  margin-bottom: -.2rem;
  font-size: 2.2rem;
  font-weight: 500;
}

.balance__date {
  color: #888;
  font-size: 1.4rem;
}

.balance__value {
  font-size: 4.5rem;
  font-weight: 400;
}

.movements {
  background-color: #fff;
  border-radius: 1rem;
  grid-row: 2 / span 3;
  overflow: scroll;
}

.movements__row {
  border-bottom: 1px solid #eee;
  align-items: center;
  padding: 2.25rem 4rem;
  display: flex;
}

.movements__type {
  text-transform: uppercase;
  color: #fff;
  border-radius: 10rem;
  margin-right: 2rem;
  padding: .1rem 1rem;
  font-size: 1.1rem;
  font-weight: 500;
}

.movements__date {
  text-transform: uppercase;
  color: #666;
  font-size: 1.1rem;
  font-weight: 500;
}

.movements__type--deposit {
  background-image: linear-gradient(to top left, #39b385, #9be15d);
}

.movements__type--withdrawal {
  background-image: linear-gradient(to top left, #e52a5a, #ff585f);
}

.movements__value {
  margin-left: auto;
  font-size: 1.7rem;
}

.summary {
  grid-row: 5 / 6;
  align-items: baseline;
  margin-top: 1rem;
  padding: 0 .3rem;
  display: flex;
}

.summary__label {
  text-transform: uppercase;
  margin-right: .8rem;
  font-size: 1.2rem;
  font-weight: 500;
}

.summary__value {
  margin-right: 2.5rem;
  font-size: 2.2rem;
}

.summary__value--in, .summary__value--interest {
  color: #66c873;
}

.summary__value--out {
  color: #f5465d;
}

.btn--sort {
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin-right: auto;
  font-size: 1.3rem;
  font-weight: 500;
  display: flex;
}

.btn--sort svg {
  width: 2.7rem;
  height: 2.7rem;
}

.operation {
  color: #333;
  border-radius: 1rem;
  padding: 3rem 4rem;
}

.operation--transfer {
  background-image: linear-gradient(to top left, #4dabf7, #99e9f2);
}

.operation--loan {
  background-image: linear-gradient(to top left, #8ce99a, #d3f9d8);
}

.operation--close {
  background-image: linear-gradient(to top left, #ff6b6b, #ffa8a8);
}

h2 {
  color: #333;
  margin-bottom: 1.5rem;
  font-size: 1.7rem;
  font-weight: 600;
}

.form {
  grid-template-rows: auto auto;
  grid-template-columns: 2.5fr 2.5fr 1fr;
  gap: .4rem 1rem;
  display: grid;
}

.form.form--loan {
  grid-template-columns: 2.5fr 1fr 2.5fr;
}

.form__label--loan {
  grid-row: 2;
}

.form__input {
  width: 100%;
  text-align: center;
  color: #333;
  background-color: rgba(255, 255, 255, .4);
  border: none;
  border-radius: .7rem;
  padding: .3rem 1rem;
  font-family: inherit;
  font-size: 1.5rem;
  transition: all .3s;
}

.form__input:focus {
  background-color: rgba(255, 255, 255, .6);
  outline: none;
}

.form__label {
  text-align: center;
  font-size: 1.3rem;
}

.form__btn {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: .7rem;
  justify-content: center;
  align-items: center;
  padding: .3rem;
  transition: all .3s;
  display: flex;
}

.form__btn:focus {
  background-color: rgba(255, 255, 255, .8);
  outline: none;
}

.logout-section {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logout-timer {
  text-align: right;
  padding: 0 .3rem;
  font-size: 1.25rem;
}

.timer {
  font-weight: 600;
}

.logout__btn {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: .7rem;
  margin-right: auto;
  padding: .5rem 2rem;
  font-family: inherit;
  transition: all .3s;
}

.logout__btn:hover, .logout__btn:focus {
  color: #fff;
  background-color: tomato;
}

